import { useState } from 'react';
import { Card, Switch, Modal, Button, Tabs } from 'antd';
import './index.less'

export default function Lend() {
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [currentToken, setCurrentToken] = useState({} as DataType);

  const { TabPane } = Tabs;
  
  function callback(key:any) {
    console.log(key);
  }
  const showModal = (item:DataType) => {
    setCurrentToken(item)
    setIsModalVisible(true);
  };

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };
  const tabList = [
    {
      key: 'Supply',
      tab: 'Supply Market',
    },
    {
      key: 'Borrow',
      tab: 'Borrow Market',
    },
  ];
  const [activeTabKey, setActiveTabKey] = useState('Supply');
  const onTabChange = (key:string) => {
    setActiveTabKey(key);
  };
  const columns = [
    {
      title: 'Asset',
      dataIndex: 'asset',
    },
    {
      title: 'APY',
      dataIndex: 'apy',
    },
    {
      title: 'Balance',
      dataIndex: 'balance',
    },
    {
      title: 'Collateral',
      dataIndex: 'collateral',
    },
  ];
  interface DataType {
    key: React.Key;
    asset: string;
    apy: number;
    balance: number;
    collateral: boolean;
    address?: string;
  }
  
  const data: DataType[] = [
    {
      key: '1',
      asset: 'Token1',
      balance: 0,
      apy: 32,
      collateral: true,
      address: '',
    },
  ];
  return (
    <div className='container mx-auto py-8 lend-container'>
      <Card className='!bg-gray-800 !text-white !border-gray-700'>
        <div className='grid grid-cols-3 gap-4'>
          <div className='text-center'>
            <div className='text-xl'>Supply Balance</div>
            <div className='text-2xl'>$0</div>
          </div>
          <div className='text-center'>
            <div className='text-2xl'>0%</div>
            <div className='text-xl'>Net APY</div>
          </div>
          <div className='text-center'>
            <div className='text-xl'>Borrow Balance</div>
            <div className='text-2xl'>$0</div>
          </div>
        </div>
      </Card>
      <Card 
        className='!bg-gray-800 !text-white !border-gray-700 !mt-8'
        tabList={tabList}
        activeTabKey={activeTabKey}
        onTabChange={key => {
          onTabChange(key);
        }}
      >
        <div>All Markets</div>
        <div className='market-list'>
          <div className='grid grid-cols-4 text-gray-100 py-4 text-base bg-gray-900 mt-4 opacity-50'>
            {
              columns.map(item => {
                return (
                  <div className='px-4' key={item.dataIndex}>{item.title}</div>
                )
              })
            }
          </div>
          <div className='text-white text-base'>
            {
              data.map(item => {
                return (
                  <div className='grid grid-cols-4 border-b border-gray-700 hover:bg-gray-600 cursor-pointer' key={item.asset} onClick={()=>showModal(item)}>
                    <div className='p-4'>{item.asset}</div>
                    <div className='p-4'>{item.apy}</div>
                    <div className='p-4'>{item.balance}</div>
                    <div className='p-4'><Switch defaultChecked={item.collateral} /></div>
                  </div>
                )
              })
            }
          </div>
        </div>
      </Card>
      <Modal title={currentToken.asset} visible={isModalVisible} onOk={handleOk} onCancel={handleCancel}>
        <div className='text-center'>
          <div className='text-xl'>{currentToken.asset}</div>
          <div className='text-md'>{`To Supply ${currentToken.asset} to the Venus Protocol, you need to approve it first.`}</div>
        </div>
        <div className='border-t border-gray-700 mt-8'>
          <Tabs defaultActiveKey="1" onChange={callback} centered>
            <TabPane tab="Supply" key="1">
              <div className='bg-gray-900 rounded-2xl p-4'>
                <div className='flex justify-between'>
                  <div className='text-xl'>Supply APY</div>
                  <div className='text-xl'>0%</div>
                </div>
                <div className='text-center mt-4 border-t border-gray-600 p-4'>
                  <Button type='primary'>Enable</Button>
                </div>
              </div>
            </TabPane>
            <TabPane tab="Withdraw" key="2">
              Content of Tab Pane 2
            </TabPane>
          </Tabs>
        </div>
      </Modal>
    </div>
  )
}