import { Select } from 'antd';
import './chg.less'
import {CHAIN_IDS_TO_NAMES, SupportedChainId} from 'constants/chains'
import useActiveWeb3React from 'hooks/useActiveWeb3React';
// import { useWeb3React } from 'web3-react-core';
export default function ChgChainBtn({className}:{className:string}) {
  const { Option } = Select;
  const handleChange = () => {
  }
  // const { account, chainId } = useWeb3React()

  const { chainId } = useActiveWeb3React()
  const chainVal = CHAIN_IDS_TO_NAMES[chainId as SupportedChainId] || 'Unsupported chain'
  return (
    <div className={className + ' chg-chain'}>
      <Select defaultValue="1" style={{ minWidth: 120 }} value={chainVal} onChange={handleChange} size="large">
        {
          Object.keys(CHAIN_IDS_TO_NAMES).map((key:number|string) => {
            const name = CHAIN_IDS_TO_NAMES[key as SupportedChainId]
            return (
              <Option value={key} key={key}>{name}</Option>
            )
          })
        }
      </Select>
    </div>
  )
}