import { Button } from "antd";
import { Layout } from 'antd';
import { useNavigate, Link, useLocation } from 'react-router-dom'
import ConnectBtn from "./connectBtn";
import ChangeChainBtn from './changeChainBtn'
export default function Header () {
  const { Header } = Layout;
  let navigate = useNavigate();
  let location = useLocation()
  console.log(location);
  let isHome = location.pathname === '/'
  return (
    <Header className="!bg-transparent z-10 flex justify-between items-center py-4 px-8 relative">
      <Link to={'/'} className="logo font-black text-4xl text-white" >D0L.finance</Link>
      {
        isHome ? (
          <Button type="primary"
            onClick={() => {
              navigate("/lend");
            }}>Launch App</Button>
        ) 
        :
        (
          <div className="flex justify-center items-center">
            <ChangeChainBtn className="!mr-2" />
            <ConnectBtn />
          </div>
        )
      }
      
    </Header>
  )
}